.navWrapper {
  width: 100%;
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  margin-bottom: 25px;
}

.navBth {
  width: 150px;
  background-color: #008966;
  border-radius: 50px;
  padding: 10px 0;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #ffffff;
}

.navBth.active {
  background-color: #00bc8c;
}